import * as THREE from 'three';
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import Stats from 'stats.js';
import * as dat from "lil-gui";

const RotateByMouse = () => {
  const canvas = document.querySelector('.rotate-by-mouse-canvas');

  if (canvas) {
    // 
    const scene = new THREE.Scene();
    
    // 
    const sizes = {
      width: window.innerWidth,
      height: window.innerHeight,
    };

    //     
    const cursor = {
      x: 0,
      y: 0,
    }

    const parametrs = {
      color: "red",
    }
    
    //      (    )
    const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height);
    camera.position.z = 3;

    //         OrbitControls   
    const controls = new OrbitControls(camera, canvas);
    //  
    controls.enableDamping = true;

    //    
    scene.add(camera);

    // 
    const geometry = new THREE.BoxGeometry(1, 1, 1, 10, 10, 10);
    const material = new THREE.MeshBasicMaterial({
      color: parametrs.color,
      wireframe: true,
    });
    const mesh = new THREE.Mesh(geometry, material);

    //    
    scene.add(mesh);

    //   FPS
    const stats = new Stats();
    stats.showPanel(0);
    document.body.appendChild(stats.dom);
  
    // lil-gui-   
    const gui = new dat.GUI();

    // -  gui 
    gui.add(mesh.scale, "x").min(0).max(5).step(0.1).name("Box scale X");
    gui.add(mesh, "visible");
    gui.add(material, "wireframe");
    gui.addColor(parametrs, "color").onChange(() => material.color.set(parametrs.color));

    //   (     )
    const renderer = new THREE.WebGLRenderer({ canvas });
    renderer.setSize(sizes.width, sizes.height);
    renderer.render(scene, camera);
  
    window.addEventListener("mousemove", (event) => {
      //        -0.5  0.5  0  
      cursor.x = -(event.clientX / sizes.width - 0.5);
      cursor.y = event.clientY / sizes.height - 0.5;
    })

    
    const tick = () => {
      //   fps
      stats.begin();

      //  
      controls.update();
      
      //   (     )
      renderer.render(scene, camera);

      //   fps
      stats.end();

      //   
      window.requestAnimationFrame(tick);
    }

    tick();

    //   
    window.addEventListener("resize", () => {
      //   
      sizes.width = window.innerWidth;
      sizes.height = window.innerHeight;

      //    
      camera.aspect = sizes.width / sizes.height;
      //   
      camera.updateProjectionMatrix();

      //  renderer
      renderer.setSize(sizes.width, sizes.height);
      renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));

      //  
      renderer.render(scene, camera);
    });

    //   
    window.addEventListener('dblclick', () => {
      if (!document.fullscreenElement) {
        canvas.requestFullscreen();
      } else {
        document.exitFullscreen();
      }
    });
  }
}


export default RotateByMouse